.edit-profile {
    padding: 4rem 2rem;
    background-color: #f5f5f5;
    min-height: 100vh;

    .header {
        text-align: center;
        margin-bottom: 4rem;

        h1 {
            font-size: 2.4rem;
            margin-bottom: 2rem;
            color: #2c3e50;
        }

        button {
            padding: 1rem 2rem;
            font-size: 1.6rem;
            font-weight: bold;
            background-color: #00bc77;
            color: #fff;
            border: none;
            border-radius: 0.3rem;
            cursor: pointer;
            transition: background-color 0.3s;

            &:hover {
                background-color: #00995f;
            }
        }

        .edit-form {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1.5rem;

            input {
                padding: 1rem;
                font-size: 1.6rem;
                border: 1px solid #ccc;
                border-radius: 0.3rem;
                width: 250px;
            }

            .buttons {
                display: flex;
                gap: 1rem;

                button {
                    background-color: #00bc77;

                    &:last-child {
                        background-color: #d9534f;

                        &:hover {
                            background-color: #c9302c;
                        }
                    }
                }
            }
        }
    }
}